import { compR } from "../func/compr";
import { iterator } from "../iterator";
import { isReduced } from "../reduced";
export function duplicate(n = 1, src) {
    return src
        ? iterator(duplicate(n), src)
        : (rfn) => {
            const r = rfn[2];
            return compR(rfn, (acc, x) => {
                for (let i = n; i >= 0 && !isReduced(acc); i--) {
                    acc = r(acc, x);
                }
                return acc;
            });
        };
}
