import { equiv as _equiv } from "@thi.ng/equiv";
import { RES_PARTIAL } from "./api";
import { result } from "./result";
export const lit = (match, success, fail, equiv = _equiv) => () => {
    const buf = [];
    const n = match.length;
    let i = 0;
    return (ctx, x) => equiv((buf.push(x), x), match[i++])
        ? i === n
            ? result(success && success(ctx, buf))
            : RES_PARTIAL
        : result(fail && fail(ctx, buf), -1 /* FAIL */);
};
