import { RES_PARTIAL } from "./api";
import { result } from "./result";
/**
 * Takes a matcher and `min` / `max` repeats. Returns new matcher which
 * only returns `Match.FULL` if `match` succeeded at least `min` times
 * or once `max` repetitions have been found.
 *
 * @param match
 * @param min
 * @param max
 * @param success
 * @param fail
 */
export const repeat = (match, min, max, success, fail) => () => {
    let m = match();
    let i = 0;
    const buf = [];
    return (ctx, x) => {
        buf.push(x);
        const r = m(ctx, x);
        if (r.type === 1 /* FULL */) {
            i++;
            if (i === max) {
                return result(success && success(ctx, buf));
            }
            m = match();
            return RES_PARTIAL;
        }
        else if (r.type === -1 /* FAIL */) {
            if (i >= min) {
                buf.pop();
                return result(success && success(ctx, buf), 2 /* FULL_NC */);
            }
            else {
                return result(fail && fail(ctx, buf), -1 /* FAIL */);
            }
        }
        return r;
    };
};
