import { Stream } from "../stream";
import { nextID } from "../utils/idgen";
/**
 * Yields a single-value stream of the resolved promise and then
 * automatically marks itself done. It doesn't matter if the promise
 * resolves before the first subscriber has attached.
 *
 * @param src
 */
export const fromPromise = (src) => {
    let canceled = false;
    let isError = false;
    let err = {};
    src.catch((e) => {
        err = e;
        isError = true;
    });
    return new Stream((stream) => {
        src.then((x) => {
            if (!canceled && stream.getState() < 2 /* DONE */) {
                if (isError) {
                    stream.error(err);
                    err = null;
                }
                else {
                    stream.next(x);
                    stream.done();
                }
            }
        }, (e) => stream.error(e));
        return () => {
            canceled = true;
        };
    }, `promise-${nextID()}`);
};
