/**
 * Merges all given maps in left-to-right order into `dest`.
 * Returns `dest`.
 *
 * @param dest
 * @param xs
 */
export const mergeMap = (dest, ...xs) => {
    for (let x of xs) {
        for (let pair of x) {
            dest.set(pair[0], pair[1]);
        }
    }
    return dest;
};
/**
 * Merges all given objects in left-to-right order into `dest`.
 * Returns `dest`.
 *
 * @param dest
 * @param xs
 */
export const mergeObj = (dest, ...xs) => Object.assign(dest, ...xs);
