import { alts } from "./alts";
import { altsLit } from "./alts-lit";
import { result } from "./result";
/**
 * Returns a single input matcher which returns `Match.FULL` if the
 * input is within the closed interval given by [`min`,`max`].
 *
 * @param min
 * @param max
 * @param success
 * @param fail
 */
export const range = (min, max, success, fail) => () => (ctx, x) => x >= min && x <= max
    ? result(success && success(ctx, x))
    : result(fail && fail(ctx, x));
/**
 * Matcher for single digit characters (0-9).
 *
 * @param success
 * @param fail
 */
export const digit = (success, fail) => range("0", "9", success, fail);
/**
 * Matcher for single A-Z or a-z characters.
 *
 * @param success
 * @param fail
 */
export const alpha = (success, fail) => alts([range("a", "z"), range("A", "Z")], undefined, success, fail);
/**
 * Combination of `digit()` and `alpha()`.
 *
 * @param success
 * @param fail
 */
export const alphaNum = (success, fail) => alts([alpha(), digit()], undefined, success, fail);
const WS = new Set([" ", "\n", "\t", "\r"]);
/**
 * Matcher for single whitespace characters.
 *
 * @param success
 * @param fail
 */
export const whitespace = (success, fail) => altsLit(WS, success, fail);
