import { copy } from "./utils";
export const mergeMapWith = (f, dest, ...xs) => {
    const res = copy(dest, Map);
    for (let x of xs) {
        for (let [k, v] of x) {
            res.set(k, res.has(k) ? f(res.get(k), v) : v);
        }
    }
    return res;
};
export const mergeObjWith = (f, dest, ...xs) => {
    const res = Object.assign({}, dest);
    for (let x of xs) {
        for (let k in x) {
            const v = x[k];
            res[k] = res.hasOwnProperty(k) ? f(dest[k], v) : v;
        }
    }
    return res;
};
