import { compR } from "../func/compr";
import { $iter } from "../iterator";
export function dropWhile(...args) {
    return ($iter(dropWhile, args) ||
        ((rfn) => {
            const r = rfn[2];
            const pred = args[0];
            let ok = true;
            return compR(rfn, (acc, x) => ((ok = ok && pred(x)) ? acc : r(acc, x)));
        }));
}
