import { LOGGER } from "../api";
import { Subscription } from "../subscription";
import { nextID } from "../utils/idgen";
/**
 * Creates a new subscription which receives promises, buffers them and
 * then passes their resolved values downstream. If the optional `fail`
 * handler is provided, it'll be called with the error of each failed
 * promise. If none is provided, the sub's `error()` handler is called,
 * which then stops the sub from receiving further values.
 *
 * ```
 * fromIterable([1, 2, 3], 100)
 *   .transform(tx.delayed(1000))
 *   .subscribe(resolve())
 *   .subscribe(trace("result"))
 * // result 1
 * // result 2
 * // result 3
 * // result done
 * ```
 *
 * @param opts
 */
export const resolve = (opts) => new Resolver(opts);
export class Resolver extends Subscription {
    constructor(opts = {}) {
        super(undefined, undefined, undefined, opts.id || `resolve-${nextID()}`);
        this.outstanding = 0;
        this.fail = opts.fail;
    }
    next(x) {
        this.outstanding++;
        x.then((y) => {
            if (this.state < 2 /* DONE */) {
                this.dispatch(y);
                if (--this.outstanding === 0) {
                    this.done();
                }
            }
            else {
                LOGGER.warn(`resolved value in state ${this.state} (${x})`);
            }
        }, (e) => {
            if (this.fail) {
                this.fail(e);
            }
            else {
                this.error(e);
            }
        });
    }
    done() {
        if (this.parent.getState() === 2 /* DONE */ && this.outstanding === 0) {
            super.done();
        }
    }
}
