import { RES_PARTIAL } from "./api";
import { result } from "./result";
/**
 * Takes an array of matchers and returns new matcher which applies them
 * in sequence. If any of the given matchers fails, returns
 * `Match.FAIL`.
 *
 * @param matches
 * @param success
 * @param fail
 */
export const seq = (matches, success, fail) => () => {
    let i = 0;
    let m = matches[i]();
    const n = matches.length - 1;
    const buf = [];
    return (ctx, x) => {
        if (i > n)
            return result(fail && fail(ctx, buf), -1 /* FAIL */);
        success && buf.push(x);
        while (i <= n) {
            const { type } = m(ctx, x);
            if (type >= 1 /* FULL */) {
                if (i === n) {
                    return result(success && success(ctx, buf));
                }
                m = matches[++i]();
                if (type === 2 /* FULL_NC */) {
                    continue;
                }
            }
            return type === -1 /* FAIL */
                ? result(fail && fail(ctx, buf), -1 /* FAIL */)
                : RES_PARTIAL;
        }
        return result(fail && fail(ctx, buf), -1 /* FAIL */);
    };
};
