import { illegalArgs, illegalState } from "@thi.ng/errors";
import { ensureReduced, isReduced, iterator, unreduced } from "@thi.ng/transducers";
export function fsm(states, ctx, initial = "start", update, src) {
    return src
        ? iterator(fsm(states, ctx, initial, update), src)
        : ([init, complete, reduce]) => {
            let currID = initial;
            let curr = states[initial]
                ? states[initial]()
                : illegalArgs(`invalid initial state: ${initial}`);
            return [
                init,
                complete,
                (acc, x) => {
                    update && update(ctx, x);
                    while (true) {
                        const { type, body } = curr(ctx, x);
                        const res = body && body[1];
                        if (type >= 1 /* FULL */) {
                            const next = body && states[body[0]];
                            if (next) {
                                currID = body[0];
                                curr = next();
                            }
                            else {
                                illegalState(`unknown tx: ${currID} -> ${body &&
                                    body[0]}`);
                            }
                            if (res) {
                                acc = reduceResult(reduce, acc, res);
                                isReduced(res) && (acc = ensureReduced(acc));
                            }
                            if (type === 2 /* FULL_NC */ && !isReduced(acc)) {
                                continue;
                            }
                        }
                        else if (type === -1 /* FAIL */) {
                            if (res) {
                                acc = reduceResult(reduce, acc, res);
                            }
                            return ensureReduced(acc);
                        }
                        break;
                    }
                    return acc;
                }
            ];
        };
}
const reduceResult = (rfn, acc, res) => {
    for (let x of unreduced(res)) {
        acc = rfn(acc, x);
        if (isReduced(acc)) {
            break;
        }
    }
    return acc;
};
