import { compR } from "../func/compr";
import { iterator1 } from "../iterator";
export function drop(n, src) {
    return src
        ? iterator1(drop(n), src)
        : (rfn) => {
            const r = rfn[2];
            let m = n;
            return compR(rfn, (acc, x) => (m > 0 ? (m--, acc) : r(acc, x)));
        };
}
