import { compR } from "../func/compr";
import { $iter } from "../iterator";
import { reduced } from "../reduced";
export function takeWhile(...args) {
    return ($iter(takeWhile, args) ||
        ((rfn) => {
            const r = rfn[2];
            const pred = args[0];
            let ok = true;
            return compR(rfn, (acc, x) => ((ok = ok && pred(x)) ? r(acc, x) : reduced(acc)));
        }));
}
