import { empty, ensureSet } from "./utils";
export const withoutKeysMap = (src, keys) => {
    const ks = ensureSet(keys);
    const dest = empty(src, Map);
    for (let p of src.entries()) {
        const k = p[0];
        !ks.has(k) && dest.set(k, p[1]);
    }
    return dest;
};
export const withoutKeysObj = (src, keys) => {
    const ks = ensureSet(keys);
    const dest = {};
    for (let k in src) {
        src.hasOwnProperty(k) && !ks.has(k) && (dest[k] = src[k]);
    }
    return dest;
};
